<template>
  <div class="hello">
    <el-container>
      <el-header
          style="text-align: left;color: #409EFF;font-size: 20px;line-height:60px;border-bottom: 4px solid #409EFF;">
        <img src="../assets/colors.png" alt=""/>
        一个只有颜色的网站 V3.0.2
        <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect">
          <el-menu-item index="1" @click="changeToRed">红</el-menu-item>
          <el-menu-item index="2" @click="changeToPurple">紫</el-menu-item>
          <el-menu-item index="3" @click="changeToGray">灰</el-menu-item>
          <el-menu-item index="4" @click="changeToBlue">蓝</el-menu-item>
          <el-menu-item index="5" @click="changeToWhite">白</el-menu-item>
          <el-menu-item index="6" @click="changeToCyan">青</el-menu-item>
          <el-menu-item index="7" @click="changeToGreen">绿</el-menu-item>
          <el-menu-item index="8" @click="changeToYellow">黄</el-menu-item>
          <el-menu-item index="9" @click="changeToChange">渐变</el-menu-item>
        </el-menu>

      </el-header>
      <br>
      <br>
      <br>
      <br>
      <br>
      <el-container>
        <el-aside width="50%">
          <el-steps :active="3" align-center >
            <el-step title="点击颜色标签选择对应种类颜色"></el-step>
            <el-step title="复制色值信息"></el-step>
            <el-step title="点击选中两种颜色，在右侧查看颜色预览"></el-step>
          </el-steps>
          <br>
          <el-skeleton v-if="filtColorList.length==0" animated/>
            <div id="colorsItems" v-for="color in filtColorList" :key="color.colorValueHex">
              <div id="oneColor" @click="chooseColor(color.colorValueHex,color.name,color.bgi)">
                <div id="colorPadding" v-bind:style="{backgroundColor:color.colorValueHex,backgroundImage:color.bgi}"></div>
                <div id="colorInfo" >
                  {{ color.name }}
                  <br>
                  {{ color.colorValueHex }}
                  <br>
                  <span v-if="color.colorValueHex.length==7">{{hexToRgb(color.colorValueHex)}}</span>
                </div>
              </div>
            </div>
          <el-backtop target=".el-aside"></el-backtop>
        </el-aside>
        <el-container>
          <el-main>
            <h2 style="color: #409EFF">颜色预览</h2>
            <div id="preview">
              <div id="preview1" >
                <div id="previewColorPadding1" v-bind:style="{backgroundColor: color1,backgroundImage: bgi1}"></div>
                <div id="previewColorInfo1">{{color1Name}}<br>{{color1}}</div>
              </div>
              <div id="preview2" >
                <div id="previewColorPadding2" v-bind:style="{backgroundColor: color2,backgroundImage:bgi2}"></div>
                <div id="previewColorInfo2">{{color2Name}}<br>{{color2}}</div>
              </div>
            </div>
            <br>
            <div id="clacItem">
              <el-button @click="calcComplementaryColours" id="halfBtn1" type="primary">计算互补色</el-button>
              <el-button @click="chooseAdjacentColor" id="halfBtn2" type="primary">选取邻近色</el-button>
              <el-button @click="calcColorHarmony" id="fullBtn" type="primary">计算色彩和谐度</el-button>
            </div>
            <br>
            <h3 style="border: 1px solid #409EFF;border-radius: 6px;margin-left:15%;color:#409EFF;padding-top: 1%;padding-bottom: 1%;" id="previewColorHarmany">色彩和谐度：{{distenceOfTwoColors}}</h3>
            <br><br>

            预览面板使用说明：<br>
            1.选中一种颜色之后点击“计算互补色”按钮，下方会出现这个颜色的互补色 <br>
            2.选中一种颜色之后点击“选取邻近色”按钮，下方会出现这个颜色的互补色 <br>
            3.选中两种颜色之后点击“计算色彩和谐度”按钮，下方会出现这两个颜色的和谐指数， <br> 你可以根据指数进行配色。一般而言，和谐度超过50一起使用比较和谐 <br>和谐度低于50一起使用则比较跳跃

          </el-main>
        </el-container>
      </el-container>
      <el-footer>
        ©ZhaoYide www.designcolors.cc {{years}}
        <br>
        参考：色谱 中科院科技情报编委会名词室.科学出版社,1957.
      </el-footer>
    </el-container>
  </div>
</template>

<script>
import $ from 'jquery'
export default {
  name: 'mainPage',
  props: {
    msg: String
  },
  data() {
    return {
      searchInputIndex: "",
      filtColorList: [],
      colorList:[],
      color1:"",
      color2:"",
      color1Name:"",
      color2Name:"",
      bgi1:"",
      bgi2:"",
      distenceOfTwoColors:""

    }
  },
  methods: {
    changeToRed() {
      this.filtColorList = this.colorList.filter(p => {
        return p.name.indexOf("红") !== -1;
      });
    },
    // changeToAll() {
    //   this.filtColorList = this.colorList.filter(p => {
    //     return p.name.indexOf("") !== -1;
    //   });
    // },
    changeToPurple() {
      this.filtColorList = this.colorList.filter(p => {
        return p.name.indexOf("紫") !== -1;
      });
    },
    changeToGray() {
      this.filtColorList = this.colorList.filter(p => {
        return p.name.indexOf("灰") !== -1;
      });
    },
    changeToWhite() {
      this.filtColorList = this.colorList.filter(p => {
        return p.name.indexOf("白") !== -1;
      });
    },
    changeToBlue() {
      this.filtColorList = this.colorList.filter(p => {
        return p.name.indexOf("蓝") !== -1;
      });
    },
    changeToCyan() {
      this.filtColorList = this.colorList.filter(p => {
        return p.name.indexOf("青") !== -1;
      });
    },
    changeToGreen() {
      this.filtColorList = this.colorList.filter(p => {
        return p.name.indexOf("绿") !== -1;
      });
    },
    changeToYellow() {
      this.filtColorList = this.colorList.filter(p => {
        return p.name.indexOf("黄") !== -1;
      });
    },
    showColorHex(name,hex){

      this.$notify.info({
        duration:3000,
        title: '颜色提示',
        message: ("Name:"+name+"               Hex:"+hex+"               "+this.hexToRgb(hex))
      });
    },
    hexToRgb(hex) {
      return 'RGB(' + parseInt('0x' + hex.slice(1, 3)) + ',' + parseInt('0x' + hex.slice(3, 5))
          + ',' + parseInt('0x' + hex.slice(5, 7)) + ')';
    },
    hexToCMYK(hex){
      let R =  parseInt('0x' + hex.slice(1, 3));
      let G =  parseInt('0x' + hex.slice(3, 5));
      let B =  parseInt('0x' + hex.slice(5, 7));
      let C = 255 - R;
      let M = 255 - G;
      let Y = 255 - B;
      let K = C<M?C:M;
      K = K<Y?K:Y;
      C= C - K;
      M = M - K;
      Y = Y - K;
      return "CMYK("+C+","+M+","+Y+","+K+")"
    },
    changeToChange(){
      this.$notify.warning({
        duration:5000,
        title: '注意！',
        message: ("渐变色不提供RGB，请使用16进制色值，RGB会在之后的版本中进行更新")
      });
      this.filtColorList = this.colorList.filter(p => {
        return p.name.indexOf("渐变") !== -1;
      });

    },
    chooseColor(hex,name,bgi){
      if(this.color1==""&&this.color2!=""){
        this.color1Name=name
        this.color1=hex
        this.bgi1=bgi;
      }
      else if(this.color1!==""&&this.color2==""){
        this.color2Name=name
        this.color2=hex
        this.bgi2=bgi;

      }
      else if(this.color1==""&&this.color2==""){
        this.color1Name=name
        this.color1=hex
        this.bgi1=bgi;

      }
      else if(this.color1!=""&&this.color2!=""){
        this.color2=""
        this.color1=""
        this.color1Name=""
        this.color2Name=""
        this.bgi1=""
        this.bgi2=""
        this.color1=hex
        this.color1Name=name
        this.bgi1=bgi;

      }


    },
    calcComplementaryColours(){
      let R =  parseInt('0x' + this.color1.slice(1, 3));
      let G =  parseInt('0x' + this.color1.slice(3, 5));
      let B =  parseInt('0x' + this.color1.slice(5, 7));
      let adverseR=255-R;
      let adverseG=255-G;
      let adverseB=255-B;
      let RGB="rgb("+adverseR+","+adverseG+","+adverseB+")"
      this.color2=RGB;
      this.color2Name=this.color1Name+"的互补色"

    },
    chooseAdjacentColor(){
      let RofColor1 =  parseInt('0x' + this.color1.slice(1, 3));
      let GofColor1 =  parseInt('0x' + this.color1.slice(3, 5));
      let BofColor1 =  parseInt('0x' + this.color1.slice(5, 7));
      RofColor1 /= 255
      GofColor1 /= 255
      BofColor1 /= 255
      const max1 = Math.max(RofColor1, GofColor1, BofColor1)
      const min1 = Math.min(RofColor1, GofColor1, BofColor1)
      const range1 = max1 - min1
      let Vofcolor1 = max1
      let Sofcolor1 = Number(Vofcolor1 == 0 ? 0 : (range1 / Vofcolor1))
      let Hofcolor1 = 0
      if (RofColor1 === Vofcolor1) Hofcolor1 = (60 * (GofColor1 - BofColor1)) / range1
      if (GofColor1 === Vofcolor1) Hofcolor1 = 120 + (60 * (BofColor1 - RofColor1)) / range1
      if (BofColor1 === Vofcolor1) Hofcolor1 = 240 + (60 * (RofColor1 - GofColor1)) / range1
      if (range1 === 0) Hofcolor1 = 0
      if (Hofcolor1 < 0) Hofcolor1 += 360
      Hofcolor1 = (Hofcolor1 / 2) * (256 / 180)
      Sofcolor1 *= 255
      Vofcolor1 *= 255
      let count;
      let r,g,b;
      let filt;
      let countHex;
    for(let i=0;i<this.colorList.length;i++){
        if(this.color1===this.colorList[i].colorValueHex){
          continue;
        }
        let RofColor2 =  parseInt('0x' + this.colorList[i].colorValueHex.slice(1, 3));
        let GofColor2 =  parseInt('0x' + this.colorList[i].colorValueHex.slice(3, 5));
        let BofColor2 =  parseInt('0x' + this.colorList[i].colorValueHex.slice(5, 7));
        RofColor2 /= 255
        GofColor2 /= 255
        BofColor2 /= 255
        const max = Math.max(RofColor2, GofColor2, BofColor2)
        const min = Math.min(RofColor2, GofColor2, BofColor2)
        const range = max - min
        let Vofcolor2 = max
        let Sofcolor2 = Number(Vofcolor2 === 0 ? 0 : range / Vofcolor2);
        let Hofcolor2 = 0
        if (RofColor2 === Vofcolor2) Hofcolor2 = (60 * (GofColor2 - BofColor2)) / range
        if (GofColor2 === Vofcolor2) Hofcolor2 = 120 + (60 * (BofColor2 - RofColor2)) / range
        if (BofColor2 === Vofcolor2) Hofcolor2 = 240 + (60 * (RofColor2 - GofColor2)) / range
        if (range === 0) Hofcolor2 = 0
        if (Hofcolor2 < 0) Hofcolor2 += 360
        Hofcolor2 = (Hofcolor2 / 2) * (256 / 180)
        Sofcolor2 *= 255
        Vofcolor2 *= 255
        let DistenceOfTwoColors;
        DistenceOfTwoColors=Math.sqrt(Math.pow((Hofcolor1-Hofcolor2),2)+Math.pow((Sofcolor1-Sofcolor2),2)+Math.pow((Vofcolor1-Vofcolor2),2))
        if(i==0){
          count=DistenceOfTwoColors;
          r=RofColor2*255;
          g=GofColor2*255;
          b=BofColor2*255;
          countHex=this.colorList[i].colorValueHex;
        }else{
          if(DistenceOfTwoColors<count){
            count=DistenceOfTwoColors;
            r=RofColor2*255;
            g=GofColor2*255;
            b=BofColor2*255;
            countHex=this.colorList[i].colorValueHex;
          }else{
            continue;
          }
        }
        filt = this.colorList.filter(p => {
          return p.colorValueHex.indexOf(countHex) !== -1;
        });
        console.log("rgb("+r+","+g+","+b+")")
        this.color2="rgb("+r+","+g+","+b+")"
        this.color2Name=filt[0].name
      }
    },
    calcColorHarmony(){
      let RofColor1 =  parseInt('0x' + this.color1.slice(1, 3));
      let GofColor1 =  parseInt('0x' + this.color1.slice(3, 5));
      let BofColor1 =  parseInt('0x' + this.color1.slice(5, 7));
      RofColor1 /= 255
      GofColor1 /= 255
      BofColor1 /= 255
      const max1 = Math.max(RofColor1, GofColor1, BofColor1)
      const min1 = Math.min(RofColor1, GofColor1, BofColor1)
      const range1 = max1 - min1
      let Vofcolor1 = max1
      let Sofcolor1 = Number(Vofcolor1 == 0 ? 0 : (range1 / Vofcolor1))
      let Hofcolor1 = 0
      if (RofColor1 === Vofcolor1) Hofcolor1 = (60 * (GofColor1 - BofColor1)) / range1
      if (GofColor1 === Vofcolor1) Hofcolor1 = 120 + (60 * (BofColor1 - RofColor1)) / range1
      if (BofColor1 === Vofcolor1) Hofcolor1 = 240 + (60 * (RofColor1 - GofColor1)) / range1
      if (range1 === 0) Hofcolor1 = 0
      if (Hofcolor1 < 0) Hofcolor1 += 360
      Hofcolor1 = (Hofcolor1 / 2) * (256 / 180)
      Sofcolor1 *= 255
      Vofcolor1 *= 255
      let RofColor2 =  parseInt('0x' + this.color2.slice(1, 3));
      let GofColor2 =  parseInt('0x' + this.color2.slice(3, 5));
      let BofColor2 =  parseInt('0x' + this.color2.slice(5, 7));
      RofColor2 /= 255
      GofColor2 /= 255
      BofColor2 /= 255
      const max = Math.max(RofColor2, GofColor2, BofColor2)
      const min = Math.min(RofColor2, GofColor2, BofColor2)
      const range = max - min
      let Vofcolor2 = max
      let Sofcolor2 = Number(Vofcolor2 === 0 ? 0 : range / Vofcolor2);
      let Hofcolor2 = 0
      if (RofColor2 === Vofcolor2) Hofcolor2 = (60 * (GofColor2 - BofColor2)) / range
      if (GofColor2 === Vofcolor2) Hofcolor2 = 120 + (60 * (BofColor2 - RofColor2)) / range
      if (BofColor2 === Vofcolor2) Hofcolor2 = 240 + (60 * (RofColor2 - GofColor2)) / range
      if (range === 0) Hofcolor2 = 0
      if (Hofcolor2 < 0) Hofcolor2 += 360
      Hofcolor2 = (Hofcolor2 / 2) * (256 / 180)
      Sofcolor2 *= 255
      Vofcolor2 *= 255
      console.log(Hofcolor1,Hofcolor2,Sofcolor1,Sofcolor2,Vofcolor1,Vofcolor2)
      let DistenceOfTwoColors;
      DistenceOfTwoColors=Math.sqrt(Math.pow((Hofcolor1-Hofcolor2),2)+Math.pow((Sofcolor1-Sofcolor2),2)+Math.pow((Vofcolor1-Vofcolor2),2))
      if(this.color1.substr(0,1)=="#"&&this.color2.substr(0,1)=="r"){
        this.color1=""
        this.color2=""
        this.color1Name=""
        this.color2Name=""
        this.$confirm("计算得出的邻近色/互补色暂不提供相似度计算，请在左侧色卡区选择2种不同颜色后重试","错误",{
          type:'error',
        })
      }
      else if(this.color1===""||this.color2===""){
        this.$confirm("选取的颜色不足两个，请在左侧色卡区选择2种不同颜色后重试","错误",{
          type:'error'
        })
      }
      else if(this.color1===this.color2){
        this.$confirm("不得选取同样的颜色，请在左侧色卡区选择2种不同颜色后重试","错误",{
          type:'error'
        })
      }
      this.distenceOfTwoColors=(100/DistenceOfTwoColors).toString().substr(0,5)*100;
      console.log(DistenceOfTwoColors)
    }

  },
  computed:{
    years(){
      var date=new Date()
      let yeaynow=date.getFullYear()
      let nextYear=yeaynow+1
      return yeaynow+"~"+nextYear
    }
  },
  mounted() {
    const that = this;
    // const json = require('../../public/colorList.json');
    // that.colorList = json;
    $.getJSON("./colorList.json",function(data){
      that.colorList=data;
      console.log(data);
    })
    console.log(that.filtColorList)
    this.$confirm('这是本网站的使用说明：<br>1.点击颜色标签选择对应种类颜色<br>2.复制色值信息<br>3.点击选中两种颜色，在右侧查看颜色预览<br>4.网站右下角有回到顶部的按钮', '提示', {
      confirmButtonText: '我知道了',
      type: 'info',
      dangerouslyUseHTMLString: true
    })
  }

}
</script>

